import { phoneCodeData } from "@/utils/phoneCode";
import { countriesData } from "@/utils/data";
import meSideMenu from "@/components/meSideMenu";
import profileTitle from "@/components/profileTitle";
import xllLoading from "@/components/xllLoading";
import { ZOHO_SYNC, GET_COUNTRY_LIST, USER_CONTACT_EDIT_V2, USER_INFO_BY_TOKEN_V2, USER_INFO_VISITOR_V2, UPLOAD_BY_ALI_OSS, UPLOAD_BY_SERVICE } from '@/api/api';
import { useStore } from "vuex";
import { ref, reactive } from "vue";
import { encode, decode } from 'js-base64';
import ImageCompressor from 'compressorjs';
export default {
  name: "userContact",
  components: {
    meSideMenu,
    profileTitle,
    xllLoading
  },

  setup() {
    const store = useStore();

    const setIdentity = data => {
      store.commit('identity', data);
    };

    const submitLoadingValue = ref(false);
    const basicForms = ref(null);
    const basicForm = reactive({
      first_name: "",
      last_name: '',
      username: '',
      identity: localStorage.getItem('identity'),
      sex: '',
      phone: '',
      email: '',
      state_id: '',
      town_id: '',
      country_id: '',
      country_info: '',
      headimgurl: '',
      language: 2,
      birthday: '',
      area_code: '+86'
    }); // const checkContactPhone=(rule,value,callback)=>{
    //   if(!value){
    //     return callback(new Error('Please input  phone'))
    //   }
    //   if(!basicForm.area_code){
    //     return callback(new Error('Please input area code'))
    //   }
    //   callback()
    // }
    // phone: [
    //   {
    //     required: false,
    //     validator:checkContactPhone,
    //     trigger: 'blur',
    //   },
    // ],

    const basicRules = reactive({
      first_name: [{
        required: true,
        message: 'First Name',
        trigger: 'blur'
      }],
      last_name: [{
        required: true,
        message: 'Last Name',
        trigger: 'blur'
      }],
      sex: [{
        required: true,
        message: 'Select your gender',
        trigger: 'change'
      }],
      email: [{
        required: true,
        message: 'Please enter your email',
        trigger: 'change'
      }],
      headimgurl: [{
        required: true,
        message: "Choose your avatar",
        trigger: 'change'
      }]
    });

    const birthdayDisabledDate = date => {
      let myDate = new Date();
      return date.getTime() >= myDate.getTime();
    };

    return {
      setIdentity,
      submitLoadingValue,
      basicForm,
      basicForms,
      basicRules,
      birthdayDisabledDate
    };
  },

  computed: {
    identity: {
      get() {
        return this.$store.state.identity;
      }

    }
  },

  data() {
    return {
      uploadLoadingStatus: false,
      userContact: {},
      countryInfo: '',
      haveLocationStatus: false,
      showLocationCancelStatus: false,
      sideMenuStatus: true,
      sexOptions: [{
        value: 1,
        object_en: 'Male',
        object_cn: '男'
      }, {
        value: 2,
        object_en: 'Female',
        object_cn: "女"
      }, {
        value: 3,
        object_en: 'Undisclosed',
        object_cn: '未公开'
      }],
      sLocationType: 1,
      //1 国外 2国内
      countryObj: {},
      provinceObj: {},
      cityObj: {},
      districtObj: {},
      countryName: '',
      countryNameCn: '',
      provinceName: '',
      provinceNameCn: '',
      cityName: '',
      cityNameCn: '',
      districtName: '',
      districtNameCn: '',
      countryOptions: [],
      provinceOptions: [],
      cityOptions: [],
      districtOptions: [],
      uploadActionUrl: process.env.VUE_APP_UPLOAD_ACTION_URL,
      uploadHeaders: {
        platform: 4
      },
      uploadData: {
        token: localStorage.getItem('token')
      },
      profilePhotoUrl: '',
      logoPhotoUrl: '',
      nationalityOptions: countriesData,
      phoneCodeData: phoneCodeData,
      i: 0,
      action: ''
    };
  },

  beforeRouteUpdate(to) {
    console.log(to.query.s);
    let str = to.query.s;

    if (str) {
      let strObj = JSON.parse(decode(str));
      console.log(strObj);
      this.i = strObj.i;
      this.action = strObj.action;

      if (strObj.action == 'add') {
        this.sideMenuStatus = false;
        this.getBasicInfo(strObj.i);
      }

      if (strObj.action == 'edit') {
        this.getBasicInfo(strObj.i);
      }
    }
  },

  mounted() {
    let str = this.$route.query.s;

    if (str) {
      let strObj = JSON.parse(decode(str)); // console.log(strObj)

      this.i = strObj.i;
      this.action = strObj.action;

      if (strObj.action == 'add') {
        this.sideMenuStatus = false;
        this.getBasicInfo(strObj.i);
      }

      if (strObj.action == 'edit') {
        this.getBasicInfo(strObj.i);
      }
    }

    this.getAllCountry();
  },

  methods: {
    changeEditLocation() {
      this.haveLocationStatus = false;
      this.showLocationCancelStatus = true;
      this.countryName = '';
      this.countryNameCn = '';
      this.provinceName = '';
      this.provinceNameCn = '';
      this.cityName = '';
      this.cityNameCn = '';
    },

    changeCancelLocation() {
      this.haveLocationStatus = true;
      this.showLocationCancelStatus = false;
      let userContact = this.userContact;

      if (userContact.country_info) {
        let countryInfoArr = JSON.parse(userContact.country_info);
        this.countryName = countryInfoArr.country_name_en;
        this.countryNameCn = countryInfoArr.country_name_cn;
        this.provinceName = countryInfoArr.province_name_en;
        this.provinceNameCn = countryInfoArr.province_name_cn;
        this.cityName = countryInfoArr.city_name_en;
        this.cityNameCn = countryInfoArr.city_name_cn;
        this.districtName = countryInfoArr.district_name_en;
        this.districtNameCn = countryInfoArr.district_name_cn;
        this.basicForm.country_info = userContact.country_info;
      }
    },

    cancelUploadProfile() {
      this.uploadLoadingStatus = false;
    },

    getVisitorInfo(uid, identity) {
      let params = {
        user_id: uid,
        identity: identity
      };
      USER_INFO_VISITOR_V2(params).then(res => {
        console.log(res);

        if (res.code == 200) {
          let userContact = res.message.user_contact;
          this.userContact = userContact;

          if (userContact) {
            this.basicForm.first_name = userContact.first_name;
            this.basicForm.last_name = userContact.last_name;

            if (userContact.username) {
              this.basicForm.username = userContact.username;
            }

            if (userContact.email) {
              this.basicForm.email = userContact.email;
            }

            if (userContact.sex) {
              this.basicForm.sex = userContact.sex;
            }

            if (userContact.phone) {
              this.basicForm.phone = userContact.phone;
            }

            if (userContact.country_id) {
              this.basicForm.country_id = userContact.country_id;
            }

            if (userContact.state_id) {
              this.basicForm.state_id = userContact.state_id;
            }

            if (userContact.town_id) {
              this.basicForm.town_id = userContact.town_id;
            }

            if (userContact.headimgurl) {
              this.basicForm.headimgurl = userContact.headimgurl;
              this.profilePhotoUrl = userContact.headimgurl;
            }

            if (userContact.birthday && userContact.birthday !== '0000-00-00') {
              this.basicForm.birthday = userContact.birthday;
            }

            if (userContact.area_code) {
              this.basicForm.area_code = userContact.area_code;
            }

            if (userContact.country_info) {
              this.basicForm.country_info = userContact.country_info;
            }
          }
        }
      }).catch(err => {
        console.log(err);
        this.$message.error(err.msg);
      });
    },

    getBasicInfo(identity) {
      let params = {
        identity: identity
      };
      USER_INFO_BY_TOKEN_V2(params).then(res => {
        // console.log(res)
        if (res.code == 200) {
          let userContact = res.message.user_contact;
          this.userContact = userContact;

          if (userContact) {
            this.basicForm.first_name = userContact.first_name;
            this.basicForm.last_name = userContact.last_name;

            if (userContact.username) {
              this.basicForm.username = userContact.username;
            }

            if (userContact.email) {
              this.basicForm.email = userContact.email;
            }

            if (userContact.sex) {
              this.basicForm.sex = userContact.sex;
            }

            if (userContact.phone) {
              this.basicForm.phone = userContact.phone;
            }

            if (userContact.country_id) {
              this.basicForm.country_id = userContact.country_id;
            }

            if (userContact.state_id) {
              this.basicForm.state_id = userContact.state_id;
            }

            if (userContact.town_id) {
              this.basicForm.town_id = userContact.town_id;
            }

            if (userContact.headimgurl) {
              this.basicForm.headimgurl = userContact.headimgurl;
              this.profilePhotoUrl = userContact.headimgurl;
            }

            if (userContact.birthday && userContact.birthday !== '0000-00-00') {
              this.basicForm.birthday = userContact.birthday;
            }

            if (userContact.area_code) {
              this.basicForm.area_code = userContact.area_code;
            }

            if (userContact.country_info) {
              let countryInfoArr = JSON.parse(userContact.country_info);
              this.countryName = countryInfoArr.country_name_en;
              this.countryNameCn = countryInfoArr.country_name_cn;
              this.provinceName = countryInfoArr.province_name_en;
              this.provinceNameCn = countryInfoArr.province_name_cn;
              this.cityName = countryInfoArr.city_name_en;
              this.cityNameCn = countryInfoArr.city_name_cn;
              this.districtName = countryInfoArr.district_name_en;
              this.districtNameCn = countryInfoArr.district_name_cn;
              this.basicForm.country_info = userContact.country_info;
              this.countryInfo = userContact.country_info;
              this.haveLocationStatus = true;
            }
          }
        }
      }).catch(err => {
        console.log(err);
        this.$message.error(err.msg);
      });
    },

    profilePhotoHttpRequest(options) {
      let self = this; // console.log(options)

      new ImageCompressor(options.file, {
        quality: 0.6,

        success(file) {
          // console.log(file)
          const formData = new FormData();
          formData.append('token', localStorage.getItem('token')); // console.log(file)

          let isInChina = process.env.VUE_APP_CHINA;

          if (isInChina === 'yes') {
            formData.append('file[]', file, file.name);
            UPLOAD_BY_ALI_OSS(formData).then(res => {
              // console.log(res)
              if (res.code == 200) {
                let myFileUrl = res.data[0]['file_url'];
                self.uploadLoadingStatus = false;
                self.profilePhotoUrl = myFileUrl;
                self.basicForm.headimgurl = myFileUrl;
              }
            }).catch(err => {
              console.log(err);
            });
          }

          if (isInChina === 'no') {
            formData.append('file', file, file.name);
            UPLOAD_BY_SERVICE(formData).then(res => {
              // console.log(res)
              if (res.code == 200) {
                let myFileUrl = res.message.file_path;
                self.uploadLoadingStatus = false;
                self.profilePhotoUrl = myFileUrl;
                self.basicForm.headimgurl = myFileUrl;
              }
            }).catch(err => {
              console.log(err);
            });
          }
        },

        error(err) {
          console.log(err.message);
        }

      });
    },

    beforeProfilePhotoUpload(file) {
      this.uploadLoadingStatus = true;
      const isLt2M = file.size / 1024 / 1024 < 20;

      if (!isLt2M) {
        this.$message.error('Avatar picture size can not exceed 20MB');
      }

      return isLt2M;
    },

    submitForm(formName) {
      // console.log(formName)
      let i = this.i;
      let action = this.action;
      this.submitLoadingValue = true;
      this.$refs[formName].validate(valid => {
        if (valid) {
          let countryObj = {
            country_name_en: this.countryName,
            country_name_cn: this.countryNameCn,
            province_name_en: this.provinceName,
            province_name_cn: this.provinceNameCn,
            city_name_en: this.cityName,
            city_name_cn: this.cityNameCn
          };
          this.basicForm.country_info = JSON.stringify(countryObj); // console.log(this.basicForm)

          let params = Object.assign({}, this.basicForm);
          USER_CONTACT_EDIT_V2(params).then(res => {
            // console.log(res)
            if (res.code == 200) {
              if (i == 1) {
                if (action == 'edit') {
                  this.$router.push('/overview?identity=' + i);
                } else {
                  let strObj = {
                    i: i,
                    action: 'add'
                  };
                  let str = encode(JSON.stringify(strObj));
                  this.$router.push({
                    path: '/educator/edit/basic',
                    query: {
                      s: str
                    }
                  });
                }
              }

              if (i == 2 || i == 3 || i == 4) {
                if (action == 'edit') {
                  this.$router.push('/overview?identity=' + i);
                } else {
                  let strObj = {
                    i: i,
                    action: 'add'
                  };
                  let str = encode(JSON.stringify(strObj));
                  this.$router.push({
                    path: '/profile/contact/company',
                    query: {
                      s: str
                    }
                  });
                }
              }

              if (i == 5) {
                if (action == 'edit') {
                  this.$router.push('/overview?identity=' + i);
                } else {
                  let strObj = {
                    i: i,
                    action: 'add'
                  };
                  let str = encode(JSON.stringify(strObj));
                  this.$router.push({
                    path: '/profile/contact/company',
                    query: {
                      s: str
                    }
                  });
                }
              }
            }
          }).catch(err => {
            console.log(err);
            this.$message.error(err.msg);
            this.submitLoadingValue = false;
          });
        } else {
          console.log('error submit!!');
          this.submitLoadingValue = false;
          return false;
        }
      });
    },

    resetForm(formName) {
      this.$refs[formName].resetFields();
    },

    getAllCountry() {
      let params = {};
      GET_COUNTRY_LIST(params).then(res => {
        console.log(res);

        if (res.code == 200) {
          this.countryOptions = res.message;
        }
      }).catch(err => {
        this.$message.error(err.msg);
      });
    },

    getAllProvinces(countryId) {
      let params = {
        country_id: countryId
      };
      GET_COUNTRY_LIST(params).then(res => {
        console.log(res);

        if (res.code == 200) {
          this.provinceOptions = res.message;
        }
      }).catch(err => {
        this.$message.error(err.msg);
      });
    },

    getAllCitys(countryId, stateId) {
      let params = {
        country_id: countryId,
        state_id: stateId
      };
      GET_COUNTRY_LIST(params).then(res => {
        console.log(res);

        if (res.code == 200) {
          this.cityOptions = res.message;
        }
      }).catch(err => {
        this.$message.error(err.msg);
      });
    },

    countryChange(e) {
      console.log(e);
      this.basicForm.state_id = undefined;
      this.basicForm.town_id = undefined;
      this.provinceOptions = [];
      this.cityOptions = [];
      this.basicForm.country_id = e.id;
      this.countryName = e.name;
      this.countryNameCn = e.name;
      this.getAllProvinces(e.id);
    },

    provinceChange(e) {
      console.log(e);
      this.basicForm.town_id = undefined;
      this.cityOptions = [];
      this.basicForm.state_id = e.id;
      this.provinceName = e.name;
      this.provinceNameCn = e.name;
      this.getAllCitys(this.basicForm.country_id, e.id);
    },

    cityChange(e) {
      console.log(e);
      this.basicForm.town_id = e.id;
      this.cityName = e.name;
      this.cityNameCn = e.name;
    },

